import * as Yup from 'yup';

export const newCourseSchema = Yup.object().shape({
    maKhoaHoc: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(20, 'Ti a 20 k t!')
        .required('Bt buc!'),
    biDanh: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(100, 'Ti a 100 k t!')
        .required('Bt buc!'),
    tenKhoaHoc: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(100, 'Ti a 100 k t!')
        .required('Bt buc!'),
    moTa: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(1000, 'Ti a 1000 k t!')
        .required('Bt buc!'),
    luotXem: Yup.number()
        .moreThan(-1, 'Khng c l s m!')
        .integer('Phi l s nguyn!')
        .required('Bt buc!'),
    danhGia: Yup.number()
        .moreThan(-1, 'Khng c l s m!')
        .integer('Phi l s nguyn!')
        .required('Bt buc!'),
    hinhAnh: Yup.mixed()
        .required('Bt buc!'),
    maDanhMucKhoaHoc: Yup.string()
        .required('Bt buc!')
});
export const userSchema = Yup.object().shape({
    soDT: Yup.string()
        .matches(/^[0-9]*$/, 'S in thoi khng hp l!')
        .required('Bt buc!'),
    email: Yup.string()
        .email('Email khng hp l!')
        .required('Bt buc!'),
    taiKhoan: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(20, 'Ti a 20 k t!')
        .required('Bt buc!')
        .matches(/^[a-zA-Z0-9_.-]*$/, 'Ch bao gm ch, s, gch di "_", gch gia "-" v du chm!'),
    hoTen: Yup.string()
        .required('Bt buc!')
        .matches(/^[a-zA-Z]{1,}(?: [a-zA-Z]+){0,2}$/, 'Vui lng nhp ng h tn ca bn (khng du)!'),
    matKhau: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .required('Bt buc!'),
    maLoaiNguoiDung: Yup.string()
        .required('Bt buc!'),
});


export const signInSchema = Yup.object().shape({
    username: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(20, 'Ti a 20 k t!')
        .required('Bt buc!')
        .matches(/^[a-zA-Z0-9_.-]*$/, 'Tn ng nhp khng hp l!'),
    password: Yup.string()
        .required('Bt buc!'),
});
export const signUpSchema = Yup.object().shape({
    phoneNumber: Yup.string()
        .matches(/^[0-9]*$/, 'S in thoi khng hp l!')
        .required('Bt buc!'),
    email: Yup.string()
        .email('Email khng hp l!')
        .required('Bt buc!'),
    username: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .max(20, 'Ti a 20 k t!')
        .required('Bt buc!')
        .matches(/^[a-zA-Z0-9_.-]*$/, 'Ch bao gm ch, s, gch di "_", gch gia "-" v du chm!'),
    fullName: Yup.string()
        .required('Bt buc!')
        .matches(/^[a-zA-Z]{1,}(?: [a-zA-Z]+){0,2}$/, 'Vui lng nhp ng h tn ca bn (khng du)!'),
    password: Yup.string()
        .min(5, 'Ti thiu 5 k t!')
        .required('Bt buc!'),
    termsAndConditions: Yup.boolean()
        .oneOf([true], "Bt buc!"),
});
